  margin: 0%;
  padding: 0%;
}

body {
  width: 100vw;
  background-color: hsl(0, 0%, 4%);
  color: hsl(0, 0%, 98%);
  font-family: "Montserrat", sans-serif;
  text-align: center;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 0px;
}

h1 {
  font-size: 11rem;
  font-weight: 300;
}

h2 {
  font-size: 4.75rem;
  font-weight: 100;
}

.hero-section {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 6.62rem;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

#hero-video {
  position: absolute;
  z-index: -1;
}

#black-overlay, #black_gradient-overlay {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
}

#black-overlay {
  background-color: hsl(0, 0%, 4%);
  opacity: 85%;
}

#black_gradient-overlay {
  background: -webkit-linear-gradient(90deg, hsl(0, 0%, 4%), hsla(0, 0%, 88%, 0));
}

#hero-display {
  max-width: 55rem;
  background: -webkit-linear-gradient(240deg, hsl(0, 0%, 88%), hsla(0, 0%, 88%, 0));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

#hero-sub_display {
  max-width: 55rem;
  background: -webkit-linear-gradient(60deg, hsl(0, 0%, 88%), hsla(0, 0%, 88%, 0));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.main_cta, .download_cta {
  overflow: hidden;
  border: 1px solid;
  border-radius: 160px;
  width: 10rem;
  height: 10rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main_cta > * {
  position: absolute;
  bottom: 2.7rem;
}

.arrow-wrapper {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}

.gallery-section {
  width: 100vw;
}

.navbar {
  position: sticky;
  z-index: 1;
  top: 0;
  width: 100vw;
  height: 3.375rem;
  background-color: hsl(0, 0%, 15%);
  display: flex;
  justify-content: center;
  align-items: center;
}

.page-padding {
  padding: 7.5rem 4rem;
}

.gallery-wrapper {
  display: flex;
  flex-direction: column;
  gap: 4rem;
}

.img-wrapper {
  display: flex;
  width: 100%;
  aspect-ratio: 16/9;
  border-radius: 30px;
  border: 3px solid hsl(0, 0%, 6%);
}

.img_prop-wrapper {
  position: relative;
  margin: 0rem auto;
  padding: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 5rem;
  align-self: end;
}

.download_cta-wrapper {
  display: none;
  position: absolute;
  top: -13rem;
}

.load_more-btn {
  background-color: hsl(0, 0%, 15%);
  border: none;
  border-radius: 16px;
  color: hsl(0, 0%, 98%);
  font-family: "Montserrat", sans-serif;
  font-size: 2rem;
  font-weight: 100;
  text-align: center;
  padding-top: 1.7rem;
  padding-bottom: 1.7rem;
  transition: 2s;
}
.load_more-btn:hover {
  background-color: hsl(0, 0%, 21%);
}
.load_more-btn:active {
  background-color: hsl(0, 0%, 15%);
  transition: 0.5s;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.scss"],"names":[],"mappings":"AA2BA;EACE,UAAA;EACA,WAAA;AA1BF;;AA+BA;EACE,YAAA;EACA,gCApCW;EAqCX,sBAhCW;EAiCX,qCAAA;EACA,kBAAA;EACA,kBAAA;AA5BF;AA8BE;EACE,UAAA;AA5BJ;;AAkCA;EACE,gBAAA;EACA,gBAAA;AA/BF;;AAkCA;EACE,kBAAA;EACA,gBAAA;AA/BF;;AAkCA;EACE,kBAAA;EACA,gBAAA;EACA,aAAA;EACA,sBAAA;EACA,YAAA;EACA,aAAA;EACA,YAAA;EA5CA,aAAA;EACA,uBAAA;EACA,mBAAA;AAcF;;AAgCA;EACE,kBAAA;EACA,WAAA;AA7BF;;AA+BA;EACE,kBAAA;EACA,WAAA;EACA,WAAA;EACA,YAAA;AA5BF;;AA+BA;EACE,gCAlFW;EAmFX,YAAA;AA5BF;;AA+BA;EA9EE,+EAAA;AAmDF;;AA+BA;EACE,gBAAA;EAhFA,iFAAA;EACA,6BAAA;EACA,oCAAA;AAqDF;;AA4BA;EACE,gBAAA;EA/EA,gFAAA;EACA,6BAAA;EACA,oCAAA;AAuDF;;AA0BA;EACE,gBAAA;EAGA,iBAAA;EACA,oBAAA;EACA,YAAA;EACA,aAAA;EApFA,aAAA;EACA,uBAAA;EACA,mBAAA;AA4DF;;AA0BA;EACE,kBAAA;EACA,cAAA;AAvBF;;AA0BA;EACE,aAAA;EACA,sBAAA;EACA,SAAA;AAvBF;;AA0BA;EACE,YAAA;AAvBF;;AA0BA;EACE,gBAAA;EACA,UAAA;EACA,MAAA;EACA,YAAA;EACA,gBAAA;EACA,iCAjImB;EAoBnB,aAAA;EACA,uBAAA;EACA,mBAAA;AAuFF;;AAwBA;EACE,oBAAA;AArBF;;AAwBA;EACE,aAAA;EACA,sBAAA;EACA,SAAA;AArBF;;AAyBA;EACE,aAAA;EACA,WAAA;EACA,kBAAA;EACA,mBAAA;EACA,gCAAA;AAtBF;;AAyBA;EACE,kBAAA;EACA,iBAAA;EACA,aAAA;EAvIA,aAAA;EACA,uBAAA;EACA,mBAAA;EAuIA,sBAAA;EACA,SAAA;EACA,eAAA;AApBF;;AAwBA;EACE,aAAA;EACA,kBAAA;EACA,WAAA;AArBF;;AA4BA;EACE,iCA9KmB;EA+KnB,YAAA;EACA,mBAAA;EACA,sBA9KW;EA+KX,qCAAA;EACA,eAAA;EACA,gBAAA;EACA,kBAAA;EACA,mBAAA;EACA,sBAAA;EACA,cAAA;AAzBF;AA2BE;EACE,iCA1Le;AAiKnB;AA2BE;EACE,iCA9LiB;EA+LjB,gBAAA;AAzBJ","sourcesContent":["$blackColor: hsla(0, 0%, 4%, 1);\n$secondaryBlackColor: hsla(0, 0%, 6%, 1);\n$tertiaryBlackColor: hsla(0, 0%, 15%, 1);\n$fourthBlackColor: hsla(0, 0%, 21%, 1);\n\n$whiteColor: hsla(0, 0%, 98%, 1);\n\n@mixin heroLinearGradient {\n  background: -webkit-linear-gradient(90deg, $blackColor,hsla(0, 0%, 88%, 0));\n}\n@mixin displayLinearGradient {\n  background: -webkit-linear-gradient(240deg, hsla(0, 0%, 88%, 1), hsla(0, 0%, 88%, 0));\n  -webkit-background-clip: text;\n  -webkit-text-fill-color: transp